:root {
  --textColor: #000;
  --yellow: #EBD96B;
  --gray: #7f7f7f;
}

body {
  font-family: 'Poppins', sans-serif;
}

.container {
  max-width: 1780px;
  margin: 0 auto;
  padding: 0 30px;
}

.subtitle {
  display: inline-block;
  position: relative;
  font-weight: 900;
  font-size: 48px;
  line-height: 1.04;
  letter-spacing: 0.06em;

  &::after {
    content: "";
    position: absolute;
    z-index: -1;
    right: 0px;
    bottom: 0px;
    width: 190px;
    height: 32px;
    background-image: url('./../img/icons/decor-yellow.svg');
    background-repeat: no-repeat;
  }
}